.home-container {
  padding-top: 40vh;
  /* Add padding to ensure content starts below the header */
  padding-left: 8vw;
  /* Add some padding on the sides */
  padding-right: 8vw;
  color: white;
  /* Set text color to white */
  padding-bottom: 25vw;
  font-family: 'Heebo', sans-serif;
}

.mini-headline {
  font-size: 5vw;
  color: rgb(0, 191, 255);
  text-align: center;
  margin-bottom: -5vw;
  padding-top: 8vh;
}

.title {
  font-family: 'Karantina', cursive;
  /* Use Karantina font */
  font-size: 16vw;
  /* Adjust font size as needed */
  color: #50565c;
  /* Dark blue color */
  margin-bottom: 3vw;
  /* Adjusted to make room for the subtitle */
  margin-top: 6vh;
  width: 100%;
  /* Make the title take up full width */
  text-align: center;
  /* Center the title */

}

.subtitle {
  font-family: 'Heebo', sans-serif;
  font-size: 7vw;
  color: grey;
  text-align: center;
  margin-bottom: 6vh;
  margin-top: 2vh;
  width: 100%;
}

.subtitle2{
  font-family: 'Heebo', sans-serif;
  font-size: 7vw;
  color: grey;
  text-align: center;
  width: 100%;
  position: relative;
  top: -6vh;
  margin-bottom: -4vh;
}

.photo-list {
  display: flex;
  flex-direction: column;
  gap: 3vh;
  /* Adjust the space between items */
}

.photo-list-item {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 2.5vh;
  /* border-bottom: 1px solid #ffffff; */
  /* Separation line */
  padding-bottom: 1vh;
  /* Adjust space at the bottom */
}

.photo-list-item:last-child {
  border-bottom: none;
  /* Remove border from the last item */
}

.photo-list-content {
  display: flex;
  flex-grow: 1;
  align-items: center;
}

.photo-list-image-homepage {
  width: 17vw;
  height: auto;
  margin-left: 7vw;
  padding-top: 3vh;
  ;
}

.photo-list-title-homepage {
  flex-grow: 1;
  text-align: right;
  padding-left: 8vw;
  font-size: 3vh;
  /* Adjust the font size */
  color: #50565c;
  /* Adjust the color as needed */
  font-weight: bolder;
}

.homePageLogos {
  height: auto;
  margin-left: 17vw;
  width: 58vw;
  position: relative;
  top: -9vh;
}

.div-list {
  position: relative;
  top: -6vh;
}

.hpArrow {
  height: auto;
  margin-left: 36vw;
  padding-top: 0vh;
  padding-bottom: 10vh;
  width: 17vw;
}


.date {
  font-size: 3vh;
  color: #50565c;
  font-weight: bolder;
  text-align: center;
  margin-bottom: 6vh;
  margin-top: 4vh;
}

.dot-line{
  position: relative;
  left: 12vw;
}